import * as React from "react";
import { styled } from "@mui/material/styles";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import { Grid, Link, Pagination } from "@mui/material";
import { useState } from "react";
import { useEffect } from "react";

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.primary.main,
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  "&:nth-of-type(odd)": {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  "&:last-child td, &:last-child th": {
    border: 0,
  },
}));
export default function DataTable({ props }) {
  const itemsPerPage = 5;
  const [currentPage, setCurrentPage] = useState(1);
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const [data, setData] = useState(
    props?.slice(indexOfFirstItem, indexOfLastItem)
  );
  const handlePageChange = (event, newPage) => {
    setCurrentPage(newPage);
  };
  useEffect(() => {
    setData(props?.slice(indexOfFirstItem, indexOfLastItem));
  }, [currentPage, props]);

  return (
    <TableContainer sx={{ mt: 1, height: "100%" }}>
      <Table sx={{ p: 1 }} aria-label="customized table">
        <TableHead>
          <TableRow>
            <StyledTableCell>Sr No</StyledTableCell>
            <StyledTableCell align="right">Uploaded Zip</StyledTableCell>
            <StyledTableCell align="right">Output Zip File</StyledTableCell>
            <StyledTableCell align="right">Output Log Zip File</StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {data?.map((row, index) => (
            <StyledTableRow key={row.folder_name}>
              <StyledTableCell component="th" scope="row">
                {currentPage > 1
                  ? itemsPerPage * (currentPage - 1) + index + 1
                  : index + 1}
              </StyledTableCell>
              <StyledTableCell align="right">
                {row.original_file_name
                  ? row.original_file_name
                  : row.folder_name}
              </StyledTableCell>
              <StyledTableCell align="right">
                <Link
                  sx={{
                    my: 2,
                    cursor: "pointer",
                  }}
                  href={"http://174.138.122.114:8000/" + row?.zip_file_url}
                >
                  Download Zip
                </Link>
              </StyledTableCell>
              <StyledTableCell align="right">
                <Link
                  sx={{
                    my: 2,
                    cursor: "pointer",
                  }}
                  href={"http://174.138.122.114:8000/" + row?.log_file_url}
                >
                  Download Log
                </Link>
              </StyledTableCell>
            </StyledTableRow>
          ))}
        </TableBody>
      </Table>

      <Grid justifyContent={"center"} display={"flex"} mt={1}>
        <Pagination
          count={Math.ceil(props.length / 5)}
          page={currentPage}
          onChange={handlePageChange}
          shape="rounded"
        />
      </Grid>
    </TableContainer>
  );
}
